<template>
  <v-container>
      <v-row>
          <v-col cols=12>
              <v-btn color="primary" to="/newtodo">
                  <v-icon>mdi-plus</v-icon>
              </v-btn>
          </v-col>
      </v-row>
      <v-row>
          <v-col cols=12>
              <v-card class="pa-3" v-if="todos.length > 0">
                  <v-list two-line dense>
                  <template v-for="(todo, i) in todos">
                  <v-list-item :key="i"  :to="`/todos/${todo._id}`">
                    <v-list-item-avatar :class="[todo.completed ? 'green darken-2': 'red darken-2']">
                    </v-list-item-avatar>

                    <v-list-item-content>
                      <v-list-item-title> {{ todo.name }}</v-list-item-title>

                      <v-list-item-subtitle>
                        {{ todo.description }}
                      </v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-action>
                        <v-btn icon @click.prevent="toggleComplete(todo)">
                            <v-icon v-if="todo.completed" color="grey lighten-1">mdi-refresh</v-icon>
                            <v-icon v-else color="green lighten-1">mdi-check</v-icon>
                        </v-btn>
                        <v-btn icon @click.prevent="deleteTodo(todo)">
                            <v-icon color="red lighten-1">mdi-delete</v-icon>
                        </v-btn>
                        </v-list-item-action>
                  </v-list-item>

                  <v-divider
                    v-if="i !== (todos.length -1)"
                    :key="`divider-${i}`"
                    inset
                  ></v-divider>
                </template>
              </v-list>
              </v-card>
              <v-card v-else>
                  <v-card-text>You have not created any Todo items. Click the plus button above to get started.</v-card-text>
              </v-card>
          </v-col>
      </v-row>
  </v-container>
</template>

<script>
export default {
    mounted(){
        this.$todos.selectAll().then(results => {
            this.todos = results
        })
    },
    data: () => ({
        todos:[]
    }),
    methods: {
        toggleComplete(todo){
            todo.completed = !todo.completed
            this.$todos.update(todo._id, todo).then(() => {
                return this.selectAll()
            })
        },
        selectAll(){
            this.$todos.selectAll().then(results => {
                this.todos = results
            })
        },
        deleteTodo(todo){
            if(confirm(`Are you sure want to delete: ${todo.name}?`)){
                this.$todos.delete(todo._id).then(() => this.selectAll())
            }
        }
    }
}

</script>

